.Poster {
  margin-right: 4px;
  display: inline-block;
  transition: transform 300ms;
}

.Poster:hover {
  transform: scale(1.2);
  border-radius: 2%;
}

.Poster img {
  max-height: 9.4vw;
  object-fit: contain;
  border-radius: 2%;
}

.Poster .imgAsPoster {
  max-height: 25vw;
}
